.footer {
  background-color: #111;
  color: #ddd;
  padding: 40px 0 0;
  margin-top: 50px;
}

.footer-container {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  padding: 0 20px;
}

.footer-section {
  max-width: 300px;
  margin-bottom: 30px;
}

.footer-logo {
  color: #d4af37;
  margin-bottom: 10px;
}

.footer-section h3 {
  margin-bottom: 10px;
  color: #fff;
}

.footer-section p,
.footer-section li {
  font-size: 14px;
  margin-bottom: 6px;
}

.footer-section ul {
  list-style: none;
  padding: 0;
}

.footer-section li {
  cursor: pointer;
}

.footer-section li:hover {
  color: #d4af37;
}

.footer-bottom {
  text-align: center;
  padding: 15px;
  background-color: #000;
  font-size: 14px;
  color: #aaa;
}