.login-container {
  min-height: 90vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f4f4f4;
}

.login-card {
  width: 350px;
  padding: 30px;
  background: white;
  border-radius: 8px;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
}

.login-title {
  text-align: center;
  margin-bottom: 20px;
  color: #333;
}

.login-input {
  width: 100%;
  padding: 10px;
  margin-bottom: 15px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 15px;
}

.login-input:focus {
  outline: none;
  border-color: #d4af37;
}

.login-btn {
  width: 100%;
  padding: 10px;
  background-color: #d4af37;
  color: black;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
}

.login-btn:hover {
  background-color: #c19a2e;
}

.login-text {
  margin-top: 15px;
  text-align: center;
  font-size: 14px;
}

.login-text span {
  color: #d4af37;
  cursor: pointer;
}